document.addEventListener('DOMContentLoaded', () => {
  app();
});

function app() {
  scrollHeader();
}
  const submitBtn = document.querySelector('.submit__btn');
  const userName = document.querySelector('#user');
  const comment = document.querySelector('#comment');
  const commentsCont = document.querySelector('.comments__container');

  submitBtn.addEventListener('click', submitFeedback);

  let feedbackArr = [];
  let positiveFeedback = false;

async function submitFeedback(e) {
  const userForm = userName.value;

  const commentForm = comment.value;

  if(userForm && commentForm !== '') {
    let newFeedback = {
      "id": Math.floor((Math.random() * 1000) + 1),
      "userName": userForm,
      "commentForm": commentForm,
    }
    feedbackArr.push(newFeedback);

    resetForm();

    addfeedback(newFeedback);
  }

  e.preventDefault();
  
}

function resetForm() {
  userName.value = '';
  comment.value = '';
}

function addfeedback(item) {
  const name = item.userName;
  const letter = name.charAt(0);
  
  const div = document.createElement('DIV');

  div.classList = "comment__card";

  div.id = item.id;

  div.innerHTML = `
    <div class="pic">${letter}</div>
          <div class="comment__info">
            <small class="nickname">
              ${item.userName}
            </small>
            <p class="comment">
              ${item.commentForm}
            </p>
          </div>
  `

  commentsCont.insertAdjacentElement('beforeend', div);
}



function scrollHeader() {
  window.document.addEventListener('scroll', () => {
    let navLinks = document.querySelectorAll('.nav__link')  

    if (window.scrollY >= 80 || window.pageYOffset >= 80){
      document.querySelector('.header').classList.add('inverted');
      document.querySelector('.nav__logo').classList.add('nav__inverted');
      navLinks.forEach(link => {
        link.classList.add('nav__inverted');
      });
    } else {
      document.querySelector('.header').classList.remove('inverted');
      document.querySelector('.nav__logo').classList.remove('nav__inverted');
      navLinks.forEach(link => {
        link.classList.remove('nav__inverted');
      });
    }
  });
}